@import "../../../main.sass";

.lattice {
    width: 100%;
    height: 100%; }

.row {
    display: flex;
    padding: 0px;
    min-width: 500px;
    margin: 0px; }

.cell {
    display: flex;
    border-width: 1px;
    border-color: #222;
    border-style: solid;
    padding: 0px;
    min-width: 15px;
    min-height: 15px;
    margin: 0px;
    span {
        display: flex;
        margin: 0px;
        min-width: 100%;
        min-height: 100%;
        padding: 0px; } }

.state0 {
    background-color: #3dcc97; }


.state1 {
    background-color: #2f75e0;
    //background-color: #ee7d7d
 }    //#afe6bf

.state2 {
    background-color: #222; }

.pause_button {
    opacity: 0.3;
    font-size: 0.85em; }

.pause_button:hover {
    opacity: 0.7; }

.pause_button:active {
    opacity: 0.9; }
