@import url('https://fonts.googleapis.com/css2?family=Raleway:wght@400;500;600;700;800&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Heebo:wght@300;400;500;700&display=swap');

$page_width: 1200px;
$main_font: 'Raleway', 'Helvetica Neue';

$white: #fff;
$black: #222;
$dark-grey: #39424E;
$font: 'Helvetica Neue','sans serif';
$body-size: 16px;
$body-text: $black;

$links: $dark-grey;
$links-hover: #050505;

$code-block: #000000;


@mixin ease($time) {
    transition: $time ease; }

@mixin skinny-hr {
    margin: 1rem 0;
    background-color: transparent;
    //box-shadow: 0 1px 0 rgba(219,219,219,0.3)
    width: 100%;
    border-style: solid;
    border-width: 1px; }


a {
    text-decoration: none; }

a:active {
    text-decoration: none; }


.header {
    width: 100%;
    margin: 30px auto;
    opacity: 0.3;
    @include ease(0.2s);
    font-weight: 600; }
.header:hover {
    opacity: 0.7;
    @include ease(0.2s); }



.nav_bar {
    display: inline-block;
    @include ease(0.2s);
    min-width: 400px;
    width: 100%;
    div {
        padding: 0px 30px 0px 0px;
        display: inline-block; }


    .name {
        float: right;
        position: relative;
        padding: 0px;
        display: inline-block; } }

* {
    margin: 0;
    padding: 0;
    -webkit-box-sizing: border-box;
    box-sizing: border-box; }

html {
    background-color: white;
    font-family: $main_font;
    font-size: 16px; }

body {
    font-size: $body-size;
    font-family: $main_font;
    line-height: 2rem;
    letter-spacing: -0.3px;
    text-shadow: none;
    opacity: 1;
    font-weight: 400;
    color: $dark-grey;
    padding: 50px 100px;
    margin: 0px auto;
    max-width: 1300px; }

h1,h2,h3 {
    margin-bottom: 0.5em;
    font-family: $main_font;
    color: $dark-grey;
    opacity: 1;
    letter-spacing: -0.3px; }

li {
    opacity: 0.8;
    & a {
        opacity: 0.9; } }

ul {
    list-style-type: none; }

p {
    display: inline-block;
    opacity: 0.8; }

.app {
    max-width: 1200px; }

a {
    color: $links;
    text-decoration: none;
    @include ease(0.09s);
    &:hover {
        color: $links-hover;
        border-bottom: 2.5px solid $links;
        font-style: none; }
    &:active {
        color: $links; } }

button {
    padding: 20px;
    border: 0px;
    border-radius: 5px;
    background: $white;
    &:hover {
        box-shadow: 5px 5px $black; } }

hr {
    @include skinny-hr;
    opacity: 0.3; }
